import React, { useEffect } from "react";
import { Link } from "react-router-dom";
import "./components.css";
import JourneyToWest from "../img/JourneyToWest.png";
import Poisonwood from "../img/Poisonwood.png";

import FingerMonkey from "../img/fingerMonkey.gif";
const BookLook = () => {
  return (
    <div className="main_container">
      <div className="item">
        <div className="img">
          <img src={JourneyToWest} alt="Jouney to West"></img>
        </div>
        <div className="des">
          <div className="br">Journey To the West</div>
          <ul>
            Key Elements:
            <li>Cloud, Energy</li>
          </ul>
          <ul>
            Inspiration:
            <li>Journey to the West</li>
          </ul>
          <ul>
            Technology used:
            <li>Game Design, Character Design, Adobe Illustrator</li>
          </ul>
        </div>
      </div>
      <div className="item">
        <div className="img">
          <img src={Poisonwood} alt="Poisonwood Bible"></img>
        </div>
        <div className="des">
          <div className="br">Emma Reyes</div>
          <ul>
            Key Elements:
            <li> Embroidery, color</li>
          </ul>
          <ul>
            Inspiration:
            <li>Emma Reyes</li>
          </ul>
          <ul>
            Technology used:
            <li> Photoshop, Adobe Illustrator</li>
          </ul>
        </div>
      </div>

      <div className="item">
        <div className="img">
          <img src={Poisonwood} alt=" Process for helping others"></img>
        </div>
        <div className="des">
          <div className="br">Poisonwood Bible</div>
          <ul>
            Key Elements:
            <li>Jungle prints, jungle creatures</li>
          </ul>
          <ul>
            Inspiration:
            <li>Poisonwood Bible</li>
          </ul>
          <ul>
            Technology used:
            <li> Photoshop, Adobe Illustrator</li>
          </ul>
        </div>
      </div>
    </div>
  );
};
export default BookLook;
